@import '../../styles/layouts.scss';
@import '../../styles/variables.module.scss';

.navbar {
  width: 100%;
  padding: $unit $unit;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1010;
  .navbar-content {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .toggle-menu-icon {
      position: relative;
      background-color: transparent;
      width: auto;
      padding: 0;
      z-index: 1100;
    }
    .call {
      position: fixed;
      bottom: calc(2 * #{$unit});
      right: calc(1 * #{$unit});
      z-index: 1010;
      display: flex;
      gap: calc(0.5 * #{$unit});
      align-items: center;
      &:hover {
        cursor: pointer;
        opacity: 0.7;
        transition: 0.3s ease-in-out;
      }
      p {
        white-space: nowrap;
        color: $accent;
      }
      .phone {
        height: calc(3 * #{$unit});
        svg {
          fill: $accent;
        }
      }
      @media (min-width: $md) {
        right: calc(2 * #{$unit});
        gap: calc(1 * #{$unit});
      }
      @media (min-width: $xxl) {
        right: calc(4 * #{$unit});
      }
    }
  }
}

@media (min-width: $md) {
  .navbar {
    padding: calc(2 * #{$unit}) calc(2 * #{$unit});
  }
}

